import React, { Fragment } from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Link } from "gatsby"
import PublicationSelection from "../components/publication-selection";
import { publicationInfoList } from "../contents/publication-info";
import { projectInfoList } from "../contents/project-info";

let bulmaCarouselScript = `<script>
bulmaCarousel.attach('#carousel-demo', {
    slidesToScroll: 4,
    slidesToShow: 4
});
</script>`;




export default ({ data }) => {
  const post = data.markdownRemark;

  let projectInfo = projectInfoList.filter((pub) => { return pub.project_tag == post.frontmatter.project_tag; })[0];

  //<div className="is-size-4">Summary</div>

  // <SEO title={post.frontmatter.title} keywords={post.frontmatter.keywords} />

  let basic_keyword_list = ["Pei-Yao Hung", "Human-Computer Interaction", "HCI", "user experience", "UX", "research"];

  /*
  role_list
  collaborator_list
  method_list
  skill_ist
  topic_list
  */


  let keyword_list = basic_keyword_list.concat(projectInfo.role_list).concat(projectInfo.method_list).concat(projectInfo.skill_list).concat(projectInfo.topic_list).concat(projectInfo.collaborator_list);


  return (
    <Layout>

      <SEO title={projectInfo.title} keywords={keyword_list} />
      <div>
        <h1 className="is-size-3 is-size-4-mobile">{projectInfo.title}</h1>
        <hr />
        <div className="content has-text-justified">
          {projectInfo.introduction != undefined? 
          <div className="hero">
            {projectInfo.introduction}<br />
          </div>
          : null}
          
          <div className="box">
            <div className="has-text-left">
            <p>My role(s): {
              projectInfo.role_list.join(", ")
            }
            </p>
            <p>{
              projectInfo.collaborator_list.length > 0 ? <Fragment>
                Collaborator(s): {
                  projectInfo.collaborator_list.join(", ")
                }
              </Fragment> : null
            }
            </p>
            </div>
          <hr />
          <div className="columns">
            <div className="column">Method(s):

              <div className="tags">
                {
                  projectInfo.method_list.map((topic, indexT) => {
                    return <span className="tag is-medium" key={indexT}>{topic}</span>
                  })
                }
              </div>


            </div>
            <div className="column">Skill(s):
            <div className="tags">
                {
                  projectInfo.skill_list.map((topic, indexT) => {
                    return <span className="tag is-medium" key={indexT}>{topic}</span>
                  })
                }
              </div>
            </div>
          </div>
          </div>
          <div>{projectInfo.summary}</div>
          <br />

          {
            projectInfo.comment_list != undefined ? projectInfo.comment_list.map((comment, index) => { return <p key={index}>{comment}</p> }) : null
          }
        </div>
        <div dangerouslySetInnerHTML={{ __html: post.html }} />
      </div>
      {
        publicationInfoList.filter((pub) => { return pub.tags.indexOf(post.frontmatter.project_tag) >= 0; }).length > 0 ?
          <Fragment>
            <h3 className="is-size-4  is-size-5-mobile" name="publications">Publications</h3>
            <div style={{height: 20}}></div>
            <PublicationSelection projectTag={post.frontmatter.project_tag}></PublicationSelection>
          </Fragment> : null
      }
      <br />
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date
        keywords
        project_tag
      }
    }
  }
`




